* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  scroll-behavior: smooth;
  background-color: #000;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.nav-wrapper {
  z-index: 1000;
  align-items: center;
  display: flex;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.side-button {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background: #8b4513;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 90px;
  padding: 12px 15px;
  font-size: 14px;
  transition: all .3s;
  display: flex;
  position: relative;
  left: 20px;
  box-shadow: 0 4px 8px #0000004d;
}

.side-button:hover {
  background: sienna;
  transform: translateY(-50%)scale(1.05);
}

.logo-image {
  object-fit: contain;
  border-radius: 6px;
  width: 80px;
  height: 80px;
  margin-bottom: 2px;
  animation: 2s infinite pulse;
  position: relative;
}

@keyframes pulse {
  0% {
    filter: drop-shadow(0 0 #fc0);
  }

  50% {
    filter: drop-shadow(0 0 10px #fc0);
  }

  100% {
    filter: drop-shadow(0 0 #fc0);
  }
}

.side-button:before {
  display: none;
}

@keyframes shine-left {
  0% {
    left: -100%;
  }

  20% {
    left: 150%;
  }

  100% {
    left: 150%;
  }
}

.nav-buttons {
  opacity: 0;
  visibility: hidden;
  background: #1e1e1ef2;
  border-radius: 8px;
  flex-direction: column;
  gap: 10px;
  width: 400px;
  padding: 20px;
  transition: all .3s;
  display: flex;
  position: relative;
  left: 30px;
  transform: translateX(-20px);
  box-shadow: 0 4px 12px #0000004d;
}

.nav-buttons.active {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.route-item {
  background: #282828e6;
  border-radius: 5px;
  align-items: center;
  gap: 15px;
  width: 100%;
  padding: 12px;
  transition: background .3s;
  display: flex;
}

.route-item:hover {
  background: #3c3c3ce6;
}

.speed {
  text-align: center;
  color: #fff;
  background: #1a1a1a;
  border-radius: 4px;
  min-width: 70px;
  padding: 5px 10px;
  font-weight: bold;
}

.route-name {
  text-align: center;
  color: #fff;
  flex-grow: 1;
  font-size: 18px;
}

.download-btn {
  color: #fff;
  cursor: pointer;
  background: #8b4513;
  border: none;
  border-radius: 4px;
  padding: 10px 25px;
  font-size: 16px;
  transition: background-color .3s;
}

.download-btn:hover {
  background: sienna;
}

.web-version {
  background: #8b4513;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  animation: 1s ease-in-out fadeIn;
}

.image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
}

.main-image {
  object-fit: contain;
  opacity: 0;
  border-radius: 8px;
  width: 500px;
  height: 1400px;
  transition: transform .3s;
  animation: 1s forwards fadeIn;
  box-shadow: 0 8px 16px #0000001a;
}

.main-image:hover {
  transform: translateY(-5px);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width <= 768px) {
  .main-image {
    width: 100%;
    max-width: 500px;
    height: auto;
  }

  .container {
    padding: 10px;
  }

  .nav-buttons {
    width: 300px;
    padding: 15px;
  }

  .route-item {
    flex-wrap: wrap;
    gap: 8px;
    padding: 10px;
  }

  .side-button {
    padding: 12px;
  }

  .logo-image {
    width: 45px;
    height: 45px;
  }

  .download-btn {
    width: 100%;
    margin-top: 5px;
    padding: 8px 15px;
    font-size: 14px;
  }

  .speed, .route-name {
    font-size: 14px;
  }
}

@media (width <= 480px) {
  .nav-buttons {
    width: 250px;
  }

  .side-button {
    width: 70px;
    padding: 10px;
    font-size: 12px;
  }

  .logo-image {
    width: 40px;
    height: 40px;
  }

  .copyright {
    font-size: 10px;
  }
}

.loader {
  z-index: 2000;
  opacity: 1;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  border: 5px solid #8b45134d;
  border-top-color: #8b4513;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
  animation: 1s ease-in-out infinite spin;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.copyright {
  text-align: center;
  color: #fff9;
  width: 100%;
  padding: 5px 0;
  font-size: 12px;
  position: fixed;
  bottom: 10px;
  left: 0;
}

.side-button.clicked {
  background: sienna;
  transform: translateY(-50%)scale(.95);
}

.download-btn.btn-clicked {
  background: sienna;
  transform: scale(.95);
}

:focus {
  outline-offset: 2px;
  outline: 2px solid #fc0;
}

:focus:not(:focus-visible) {
  outline: none;
}

.route-name, .copyright, .side-button div {
  text-shadow: 0 1px 2px #000c;
}

.download-btn:hover, .download-btn:focus {
  background: sienna;
  transform: scale(1.05);
  box-shadow: 0 4px 12px #0000004d;
}
